import DataServices from "../../../services/DataService";
let buttons =
    [
    {
        title: 'Add to Talent Pool',
        iconClass: 'fa fa-plus',
        callbackfunction: 'AddTalentPool',
        additionalClass: '',
        isdisabled: false,
        isvisible: true,
        checkPrivilege: true,
        id:'AddTalentPool',
         
    },
    {
        title: 'Delete',
        iconClass: 'fa-trash-o',
        callbackfunction: 'commonDeleteData',
        additionalClass: '',
        isdisabled: true,
        isvisible: true,
        checkPrivilege: false,
        id:'DeleteMultiple',
        isEnabledOnSelectedRow: true
    },
    ];
    let listActions=[  
        {
            title:'Edit',   
            href: 'javascript:void(0)',         
            icon: 'fa fa-pencil action-icon',
            callbackfunction:'EditTalentPool',
            additionalSpan:''
        },
        {
            title:'View',   
            href: 'javascript:void(0)',         
            icon: 'fa fa-eye action-icon',
            callbackfunction:'ClickOnView',
            additionalSpan:''
        },
        {
            title:'Add As Candidate',
            callbackfunction:'AddAsCandidate',       
            icon: 'fa fa-mail-forward action-icon',
            additionalSpan:''
        }
    ];
    let leftsearchSchema = [{
        fieldName: 'Talent Pool Name',
        fieldType: "text",
        fieldIcon: "fa-envelope",
        fieldSearchConditionName: ['TJA.FIRST_NAME'],
        isSearch: false,
        value: "",
        listOptions: []
    }, {
        fieldName: 'Email',
        fieldType: "text",
        fieldIcon: "fa-file",
        fieldSearchConditionName: ['email_id'],
        isSearch: false,
        value: "",
        listOptions: []
    }, 
    {
        fieldName: 'Department',
        fieldType: "ddl-check",
        fieldIcon: "fa fa-file-text-o",
        fieldSearchConditionName: ['TD.DEPARTMENT_ID'],
        isSearch: false,
        value: "",
        listOptions: []
    }
    
    ];
    async function  bindleftCommonSearchdropdown(instance,modulename, filtername) {
        var vm = instance;
        let params= `ids=&moduleName=${modulename}&type=${filtername}&search=&refId=`
       await DataServices.GetDDLData(params)
            .then(function (response) {
            var json = response.data;
                if (json != null) {
                    if (filtername == "HR_DEPT") {
                        if(json !=""){
                        vm.leftSearchFields[2].listOptions = json.DATA;
                        }
                    }
                }
            });
    }
    export default { buttons, listActions, leftsearchSchema, bindleftCommonSearchdropdown};