<template>
    <section>
        <tg-list ref="tgList" v-show="IsTalentPoolView" :IsShowAction="true" ModuleName="Hiring"
            SubModuleCode="HR_TALENT_POOL" :LegendArray="[]" :RenderRowActionMethod="renderActions"
            v-on:ActionButtonClick="actionButtonClick" :widgets="[]" IdentityColumn="applicant_id" :ViewList="ViewList"
            v-on:ApplyView="OnApplyView" :HeaderText="$t('TalentPool')" :listType="['List']" :ListData="TalentPoolList"
            :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
            :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp"
            :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'status_id'">
                    <span v-bind:class="getStatusClassList(data.row.status_id, 'status_id')"
                        v-if="data.row.status_id == 'Active'">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </span>
                    <span v-bind:class="getStatusClassList(data.row.status_id, 'status_id')"
                        v-if="data.row.status_id == 'InActive'">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'resume_path'">
                    <a href="#" v-if="data.column.COLUMN_NAME =='resume_path' && data.row.resume_path!=null && data.row.resume_path!=''" v-on:click="DownloadResume(data.row.resume_path,data.row.resume_name)" class="text-primary"><em class="fa fa-download pr-2"></em><span>{{data.row.resume_name}}</span></a>
                    <span v-if="data.column.COLUMN_NAME=='resume_path' && (data.row.resume_path==null || data.row.resume_path=='')" class="text-secondary"><em class="fa fa-download pr-2"></em><span>{{data.row.resume_name}}</span></span>
                </template>
            </template>
        </tg-list>
        <viewlayout v-if="ShowManageView" v-show="ShowManageView" submodulecode="HR_TALENT_POOL" modulename="Hiring"
            v-on:close="ShowManageView = false" v-bind:callbackfunction="FetchData" :usertype="usertype">
            <h5 slot="header">{{ $t('Search') }}</h5>
        </viewlayout>
    </section>
</template>
<script>
import Schema from "./TalentPoolSchema"
import DataServices from "@/services/DataService";
import viewlayout from "../../../components/Common/Modal/view-layout.vue";
export default {
    components: {
        viewlayout
    },
    data() {
        return {
            IsTalentPoolView: true,
            showExpanded: '',
            ViewList: [],
            MimeType:'',
            noRecords: false,
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            ShowManageView: false,
            searchCondition: '',
            TalentPoolList: [],
            GroupStart: 1,
            GroupEnd: 3,
            Headers: [],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            user_type: null,
            user_id: null,
            companyId: null,
            search: ''
        }
    },
    created: async function () {
        var vm = this;
        vm.user_id = this.GetUserInfo.ID
        vm.user_type = this.GetUserInfo.UserType
        vm.companyId = this.GetUserInfo.CompanyId;
        await Schema.bindleftCommonSearchdropdown(vm, "HIRING", "HR_DEPT");
        await vm.FetchData();
        this.GetDdlRecords_ViewList();
    },
    methods: {
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        actionButtonClick(callbackfunction, item, index, event) {
            switch (callbackfunction) {
                case 'EditTalentPool':
                    this.EdittoTalentPool(item.applicant_id,null)
                    break;
                case 'ClickOnView':
                    this.TalentPoolDetail(item.applicant_id);
                    break;
                case 'AddAsCandidate':
                    this.AddAsCandidate(item.applicant_id);
                    break;
            }
        },
        ManageView: function () {
            var vm = this;
            vm.ShowManageView = true;
            setTimeout(function () {
                $("#modalManageView").modal("show");
            }, 1000);
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'ClickOnView':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        DownloadResume: async function (path, name) {
            var vm=this;
            var fileName = name;
            var filePath = path;
            if (filePath == "" && fileName == "" || filePath == null) {
                vm.ShowAlert(vm.$t("NoAttachment"), "warning", true, vm.$t("Alert"));
            } else {
                var url = `filePath=${filePath}&fileName=${fileName}`;
                await DataServices.DownloadFile(url).then(async function (response) {
                    const byteData = response.data;
                    const fileURL = `data:image/png;base64;base64,${byteData}`;
                    var fileLink = document.createElement('a');
                    fileLink.href = fileURL;
                    fileLink.setAttribute('download', fileName);
                    document.body.appendChild(fileLink);
                    fileLink.click();
                })
            }
        },
        FetchData: async function () {
            var vueObj = this;
            if (!vueObj.isBlank(vueObj.searchCondition)) {
                vueObj.PageNumber = 1;
            }
            var url = `sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}&firstname=&deptname=&langCode=EN`;
            if (!vueObj.isBlank(this.searchCondition)) {
                url += `&searchCondition=${this.searchCondition}`;
            }
            await DataServices.GetTalentPoolList(url).then(function (response) {
                if (response.data != null && response.data != "") {
                    vueObj.Headers = response.data.schema;
                    vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "status_id") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == "resume_path") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                    });
                    vueObj.TalentPoolList = response.data.data;
                    vueObj.Headers.forEach((item) => {
                        if (item.DATA_TYPE == 'date') {
                            item.DATA_TYPE = 'DATE';
                        }
                        if (item.DATA_TYPE == 'select' || item.DATA_TYPE == 'string') {
                            item.DATA_TYPE = 'text';
                        }
                    })
                    if (vueObj.TalentPoolList.length > 0) {

                        vueObj.TalentPoolList.forEach(function (row) {
                            if(row.is_sample_data === true){
                                row.additionalClass = "sample-bar";
                            }
                        });

                        vueObj.TotalRecords = response.data.data[0].total_records;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.listheaderbuttons[1].isdisabled = true;
                        vueObj.noRecords = false;
                    }
                    else {
                        vueObj.noRecords = true;
                        vueObj.TotalRecords = 0
                    }
                } else {
                    vueObj.noRecords = true;
                }
            });
        },
        AddAsCandidate: function (value) {
            var vm = this;
            vm.$router.push({ name: 'HRAddAsCandidate', params: { id: value } });
        },
        TalentPoolDetail: function (value) {
            var vm = this;
            vm.$router.push({ name: 'HRTalentPoolDetail', params: { id: value, type: 'TalentPoolDetail' } });
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "ManageView":
                    this.ManageView();
                    break;
                case "AddTalentPool":
                    this.AddtoTalentPool();
            }
        },
        GetDdlRecords_ViewList: async function () {
            var vm = this;
            vm.ViewList = [];
            var url = `moduleName=Hiring&subModuleCode=HR_TALENT_POOL&viewId=0&pageSize=100&pageNum=1&sortBy=&sortExp=`;
            await DataServices.GetViewList(url).then(function (response) {
                if (response.data && typeof (response.data.DATA) != 'undefined') {
                    vm.ViewList = response.data.DATA;
                }
                var chkVACTIVE = 0;
                vm.ViewListId = "0";
                if (vm.ViewList == null || vm.ViewList == "") {

                }
                else {
                    vm.ViewList.forEach(function (item) {

                        if (item.IS_SLECTED_USER_ID > 0) {
                            chkVACTIVE = chkVACTIVE + 1;
                            vm.ViewListId = item.VIEW_ID;
                        }
                    });
                }
                var indx = 0;
                if (chkVACTIVE > 0) {
                    vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 0, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });
                }
                else {
                    if (vm.ViewList == null || vm.ViewList == "") {

                        vm.ViewList.push({ "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });
                    }
                    else {
                        vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });

                    }
                }
                setTimeout(() => {
                    if (vm.ViewListId > 0) {
                        vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = vm.ViewListId;
                    } else {
                        vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = "0";
                    }
                }, 1000);

            });
        },
        DefaultView: function () {
            var vm = this;
            var obj = {
                ModuleName: 'Hiring',
                SubmoduleCode: 'HR_TALENT_POOL'
            };
            var objectPostString = JSON.stringify(obj);
            DataServices.ClearView(objectPostString).then(function (response) {
                if (response.data != null) {
                    if (response.data.code == "1" || response.data.Code == "1") {
                        vm.ShowAlert(vm.$t("ApplySuccess", [vm.$t("DefaultView")]), 'success', true, vm.$t('Alert'));
                        vm.FetchData();
                    }
                    else {
                        vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
                    }
                }
                else {
                    vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
                }
            });
        },
        OnApplyView(viewId) {
            var vm = this;
            if (viewId == '0') {
                vm.DefaultView()
            }
            else {
                var obj = {
                    ViewId: viewId,
                    ModuleName: 'Hiring',
                    SubmoduleCode: 'HR_TALENT_POOL'
                };
                var objectPostString = JSON.stringify(obj);
                DataServices.ApplyView(objectPostString).then(function (response) {
                    if (response.data != null) {
                        if (response.data.code == "1" || response.data.Code == "1") {
                            vm.ShowAlert(vm.$t("ApplySuccess", [vm.$t("View")]), 'success', true, vm.$t('Alert'));
                            vm.$emit('RefreshManageView');
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                        }
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                    }
                });

            }
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchData();
        },
        commonDeleteData: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Crew")), function () {
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "Hiring",
                        subModuleCode: "HR_TALENT_POOL"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.CommonCommonDelete(objectPostString).then(response => {

                        if (response.data[0].Status == "Success") {
                            vm.FetchData();
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('TalentPool')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('TalentPool')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
            else {
                alert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center text-white d-block bg-success';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center text-white d-block bg-danger';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        AddtoTalentPool: function () {
            this.$router.push({ name: 'ManageTalentPool', params: { reqfrom: 'HR/TalentPoolList', id: 0, pid: null, aid: null, uid: null } });
        },
        EdittoTalentPool: function (id, pid) {            
            this.$router.push({ name: 'ManageTalentPool', params: { reqfrom: 'HR/TalentPoolList', id: id, pid: pid, aid: null, uid: null } });
        }
    }
}
</script>
